body, html {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  padding: 0;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  position: relative;
}

.stopwatch__minutes-hand {
  transform-origin: bottom;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 600s steps(60, end) infinite rotate;
  position: absolute;
  top: 25%;
  left: 48%;
}

.stopwatch__seconds-hand {
  transform-origin: bottom;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 10s linear infinite rotate;
  position: absolute;
  top: 2.1%;
  left: 49%;
}

.stopwatch__center-circle {
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
  top: calc(50% - 2.5vmin);
  left: calc(50% - 2.5vmin);
}

.stopwatch.speed-up__minutes-hand {
  animation-duration: 600s;
}

.stopwatch.speed-up__seconds-hand {
  animation-duration: 10s;
}

@keyframes rotate {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

